
.footer{
    width: 100%;
    padding: 6rem 0;
    background: rgba(0, 0, 0, 0.9);
    
}

.footer-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    grid-gap: 40px;
}

.footer .footer-container-left{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;

}

.footer .footer-container-right{
    height: 100%;
    max-width: 100%;
    display: flex;
    flex-direction: column;
    padding: 1rem;
    margin: auto;
}

.footer .location{
    display: flex;
    align-items: center;
}

.footer .location p{
    padding-bottom: 0.5rem;
}

.footer .location h4{
    padding-top: 0;
}

@media screen and (max-width:640px){
    .footer-container{
        grid-template-columns: 1fr;
    }

    .footer-container-right .social {
        margin: auto;
    }
}



