
#video{
    position: fixed;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
}

.hero {
    height: 100vh;
    width: 100%;
    object-fit: contain;
}

.hero .content {
    text-align: center;
    position: absolute;
    margin: auto;
    width: 100%;
    height: 100%;
    top: 40vh;

}

.hero .content h1{
    font-size: 5rem;
    color: #fff;
}

.hero .content h2{
    font-size: 2rem;
    color: #fff;
}

.hero p {
    font-size: 1.8rem;
    font-weight: 200;
    color: #fff;
    margin-bottom: 1.6rem;
}

.hero .btn {
    margin: 1rem .2rem;
}

@media screen and (max-width:640px){
    .content h1{
        font-size: 3rem;
    }

    .content p{
        font-size: 1.5rem;
        margin-bottom: 1.6rem;
    }
}

