.pricing{
    width: 100%;
    padding: 6rem 1rem;
    background-color: black;

}

.card-container{
    max-width: 1140px;
    margin: auto;
    display: grid;
    grid-gap: 40px;
    grid-template-columns: repeat(3, 1fr);

}

.card-container .card{
    border: 1px solid #eaeaea;
    color: #eee;

}

.card{
    text-align: center;
    padding: 1rem;
}

.card:hover{
    background-color: #141414;
}

.card h3{
    font-size: 1.4rem;
    padding: 1rem;
}

.card .bar{
    border-bottom: 1px solid #eaeaea;
    width: 10%;
    margin: 1.5rem auto;
    display: block;
}

.card .money {
    font-size: 4rem;
    font-weight: 600;
    padding: 1rem;

}

.pricing p{
    color: #eaeaea;
    padding: 1rem 0;
    font-size: 1.2rem;

}

.card .btn{
    display: block;
    width: 80%;
    margin: 2rem auto;

}

@media screen and (max-width: 790px){
    .card-container{
        max-width: 90%;
        margin: auto;
        grid-template-columns: 1fr;

    }
    
}



